/* Basic Styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: red;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  color: orange;
}

/* Form Styling */
form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

form input,
textarea {
  width: 100%;
  padding: 10px;
  margin: 8px 0;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form button {
  padding: 10px;
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

form button:hover {
  background-color: #218838;
}

/* Error Message */
.error-message {
  color: #e74c3c;
  text-align: center;
  margin-top: 10px;
}

/* Registration List */
.registration-list {
  margin: 20px;
  font-family: Arial, sans-serif;
}

.registration-list table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.registration-list th,
.registration-list td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
  font-size: 14px;
}

.registration-list th {
  background-color: #f4f4f4;
  font-weight: bold;
}

/* Total Section */
.total-section {
  margin-top: 20px;
  padding: 15px;
  background-color: #f8f8f8;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.total-section h3 {
  margin-bottom: 15px;
}

.total-section p {
  font-size: 16px;
  color: #333;
  margin: 5px 0;
}

/* Buttons Section */
.buttons-section {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.download-btn,
.print-btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.download-btn:hover,
.print-btn:hover {
  background-color: #0056b3;
}

/* Edit Form */
.edit-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  max-width: 400px;
  margin: 0 auto;
}

.edit-form h3 {
  text-align: center;
  margin-bottom: 20px;
}

.edit-form label {
  display: block;
  font-size: 14px;
  margin-bottom: 8px;
}

.edit-form input {
  width: 100%;
  padding: 8px;
  font-size: 14px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.edit-form button {
  width: 100%;
  padding: 10px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.edit-form button:hover {
  background-color: #2980b9;
}

/* Responsive Design */

/* Mobile (Max 600px) */
@media (max-width: 600px) {
  form {
    width: 90%;
  }

  .buttons-section {
    flex-direction: column;
    align-items: center;
  }

  .buttons-section button {
    width: 100%;
  }

  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
}

/* Tablet (Min 768px - Max 1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
  .buttons-section {
    justify-content: space-between;
  }

  .buttons-section button {
    width: 45%;
  }
}

/* Desktop (Min 1025px) */
@media (min-width: 1025px) {
  .registration-list {
    padding: 20px;
  }
}
